'use client'

import { useEffect, useState } from 'react'
import { Button } from '@/components/ui/button'

declare global {
  interface Window {
    Calendly: any;
  }
}

export function CalendlyButton() {
  const [isCalendlyReady, setIsCalendlyReady] = useState(false)

  useEffect(() => {
    const script = document.createElement('script')
    script.src = 'https://assets.calendly.com/assets/external/widget.js'
    script.async = true
    script.onload = () => setIsCalendlyReady(true)
    document.body.appendChild(script)

    return () => {
      document.body.removeChild(script)
    }
  }, [])

  const openCalendlyPopup = (e: React.MouseEvent) => {
    e.preventDefault()
    if (isCalendlyReady && window.Calendly) {
      window.Calendly.initPopupWidget({
        url: 'https://calendly.com/netwitx/30min?hide_event_type_details=1&hide_gdpr_banner=1&primary_color=22c55e'
      })
    } else {
      console.error('Calendly is not ready yet')
    }
  }

  return (
    <Button 
      onClick={openCalendlyPopup} 
      className="bg-[#22c55e] text-white hover:bg-[#22c55e]/90"
      disabled={!isCalendlyReady}
    >
      Book a call
    </Button>
  )
}



